html{height:100%}body{text-align:center;height:100%;font-family:'PT Sans', sans-serif;font-size:16px}#root{height:100%}h1{text-align:center;font-size:2rem;text-transform:uppercase;font-family:'Montserrat', sans-serif}a{color:#aaa;text-decoration:none}a:hover{color:#fff;text-decoration:none}

body{margin:0}#container{width:800px;margin-left:30px;margin-right:auto;margin-top:30px;padding:30px;box-shadow:3px 3px 7px #222}svg{background-color:white;padding-left:20px}.ca-map path{fill:#264653;stroke:#fff}path:hover{cursor:pointer}#tooltip{width:150px;height:auto;padding:5px;background-color:#fff;color:#264653;border-radius:5px;box-shadow:3px 3px 8px rgba(0,0,0,0.4);pointer-events:none;position:absolute}#tooltip.hidden{display:none}#tooltip p{margin:0;font-size:14px;line-height:18px}

.logo{z-index:99;text-align:center;width:100px;height:100px;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}

.arc text{font:10px sans-serif;text-anchor:middle;color:#fff}.arc path{stroke:#fff}.title{fill:teal;font-weight:bold}#chart{text-align:center}.monthArc{fill:white;stroke:#AAAAAA}.dataText{z-index:999}.dataArc{z-index:-10}

.footer{font-size:1rem;text-decoration:none;color:#fff}

.row{display:flex;flex-direction:row;flex-wrap:wrap;width:100%;height:100%;justify-content:center}.info-background{background:-webkit-gradient(linear, left bottom, left top, from(#597986),to(#264653));background:linear-gradient(bottom, #597986,#264653);color:#fff}.map-background{color:#264653}.column{display:flex;flex-direction:column;flex-basis:100%;padding:20px}.small-column{flex:1 1;min-width:250px}.large-column{flex:2 1}

